import React from "react";
import './calculator.css';
export default function Calculator(){
    var display_value="";
		var buttons=document.getElementsByTagName('button');
		var item;
		 function move(event)
{
			var btntxt=event.target.innerText;
		if (btntxt=="X")
	{
			btntxt= '*';
			display_value+=btntxt;
			document.getElementById('display').value+=btntxt;
	} 
		else if(btntxt=="=")
	{
             document.getElementById('display').value=eval(display_value);
             display_value=document.getElementById('display').value;
	}
		else if(btntxt=="C")
	{
			document.getElementById('display').value="";
			display_value="";
	}
		else if (btntxt=="sine")
	{
			btntxt= 'sin';
			display_value+=Math.sin();
			document.getElementById('display').value+=btntxt;
	}
		else
	{
			document.getElementById('display').value+=btntxt;
			display_value+=btntxt;
	}
}
			
    return(
        <>
        <div id="container">
		<h1>calculator</h1>
		<input type="text" name="display" id="display"/>	<br/>
					<button onClick={move}>
						1
					</button>
					<button onClick={move}>
						2
					</button>
					<button onClick={move}>
						3
					</button>
					<button class="sign" onClick={move}>
						+
					</button>
					<button onClick={move}>
			            4
					</button>
					<button onClick={move}>
						5
					</button>
					<button onClick={move}>
						6
					</button>
					<button class="sign" onClick={move}>
						-
					</button>
					<button onClick={move}>
						7
					</button>
					<button onClick={move}>
						8
					</button>
					<button onClick={move}>
						9
					</button>
					<button class="sign" onClick={move}>
						/
					</button>
					<button onClick={move}>
						sin
					</button>
					<button onClick={move}>
						cos
					</button>
					<button onClick={move}>
						tan
					</button>
					<button class="sign" onClick={move}>
						C
					</button>
					<button onClick={move}>
						0
					</button>
					<button class="sign" onClick={move}>
						X
					</button>
					<button class="sign" onClick={move}>
						=
					</button>
	</div>
        </>
    );
}