import React from 'react'
export default function TodoApp(){
    const addTodo = ()=>{
        const node = document.createElement("li");
        const textnode = document.createTextNode(document.getElementById('todo_text').value);
        node.appendChild(textnode);
        document.getElementById('todo_list').appendChild(node);
        document.getElementById('todo_text').value = '';
    }
    const clear = ()=>{
        document.getElementById('todo_list').innerHTML = '';
        document.getElementById('todo_text').value = ''
    }
    return(
        <>
        <div className='todoapp'>
            <div>
                <h2>Enter tasks to do in the below textarea</h2>
                <textarea id="todo_text" rows="5"></textarea>
            </div> 
            <button onClick={addTodo} value="hiii">append</button>
            <button onClick={clear} value="hiii">clear list</button>
            <ul id="todo_list">

            </ul>
        </div>
        </>
    )
}