import React from 'react';
import {Link} from "react-router-dom";
export default function Navbar(props){
    return(
    <>
    <div className='navbar' >
        <ul>
            <li>
               <Link to="/TodoApp">{props.TodoApp}</Link> 
            </li>
            <li>
                <Link to="/Textarea">{props.TextManipulator}</Link> 
            </li>
            <li>
                <Link to="/Solo">{props.SoloLearn}</Link> 
            </li>
            <li>
                <Link to="/SwIndex">{props.ShoppingWebsite}</Link> 
            </li>
            <li>
                <Link to="/Calculator">{props.Calculator}</Link>
            </li>
            <li>
                <Link to="/OpenAI">{props.OpenAI}</Link>
            </li>
            <li>
                <Link to="/TicTacToe">{props.TicTacToe}</Link>
            </li>
        </ul>
    </div>
    </>
    )
}