import React from 'react';
import './styles.css';
import ChatLogo from './chat_logo.png';
import LocationLogo from './location_logo.png';
import SocialMediaLogo from './social_media_logo.png';
import  VideoCallLogo from './video_call_logo.png';
export default function Solo(){
    return(
        <>
    <header>
      <h1>Awesome App</h1>
      <h2>This App Will Change Your Life</h2>
      <button id="download_button" onclick="cl()">
        Download Now
      </button>
  </header>
  <section className="feature">
    <div className="properties">
      <img src={ChatLogo} alt="can not display"/>
      <p>Chat with <i>Friends</i></p>
    </div>
    <div className="properties">
      <img src={LocationLogo } alt="can not display"/>
      <p>Share<i>Location</i></p>
    </div>
    <div className="properties">
      <img src={VideoCallLogo} alt="can not display"/>
      <p>Chat on <i>Video Call</i></p>
    </div>
    <div className="properties">
      <img src={SocialMediaLogo} alt="can not display"/>
      <p>Share on <i>Social Media</i></p>
    </div>
  </section>
  <section className="quote">
    <div>
      Nothing influences people more than a recommendation <b><i>from a Trusted Friend.</i></b>
    </div>
    <cite>Mark Zukerberg</cite>
  </section>
  <footer>
    <div>
      Home
    </div>
    <div>
      About
    </div>
    <div>
      Contact Us
    </div>
    <div>
      <a href="#download_button">Download the App</a>
    </div>
  </footer>




        </>
    );
}