import React,{useState} from 'react'


export default function Textarea(props){
    const handleUpClick =()=>{
        let newtext = text.toUpperCase();
        setText(newtext);
    }
    
    const handleOnChange = (event)=>{
        setText(event.target.value);
    }
    const[text,setText] = useState("this  is the first text here");
    return(
        <div>
            <h3>{props.heading}</h3>
            <div>
             <textarea rows="9" value={text} onChange={handleOnChange}>
                
              </textarea> 
            </div>
            <button onClick={handleUpClick}>
                CLICK ME FOR UPPERCASE
            </button>
            <div>
                <h3>
                    preview
                </h3>
                <p>
                    {text}
                </p>
            </div>
        </div>
    )
}